import { useState, useContext } from "react";
import styled from "styled-components";
import Styles from "../../../../base/design/styles";
import DataUtil from "../../../../utils/dataUtil";
import { ThemaTreeContext } from "../backingLibraryTab";
import ThemaUtil from "../themaUtil";
import TreeUtil from "../treeUtil";
import ThemaEditTabUtil from "./themaEditTabUtil";

const BaseFormTab = (props: {
    themaList: ThemaUtil.LevelBase[];
    addThema: (thema: ThemaUtil.LevelBase) => void;
}) => {
    const { node, setFocusNode, invalidate, setThamaData, dispatcher } = useContext(ThemaTreeContext);
    const baseNode = (node.depth === 0 ?
        node : node.parent) as TreeUtil.ElementNode;
    const baseData = baseNode.data as ThemaUtil.LevelBase;
    const [metSusNode, metPatNode] = [baseNode.children[0], baseNode.children[1]];
    const [metSusData, metPatData] = [metSusNode.data as ThemaUtil.LevelMethod, metPatNode.data as ThemaUtil.LevelMethod];

    const [presetSust, setPresetSust] = useState<string>('');
    const [presetPatt, setPresetPatt] = useState<string>('');
    const [presetSustList, setPresetSustList] = useState<string[]>(metSusData.presets.slice());
    const [presetPattList, setPresetPattList] = useState<string[]>(metPatData.presets.slice());

    const [themaName, setThemaName] = useState(baseData.themaName);

    return (<>
        <ThemaEditTabUtil.ButtonDiv buttons={[
            {
                name: 'Export',
                isEnable: true, callback: () => {
                    const options = {
                        types: [
                            {
                                accept: {
                                    'text/plain': ['.lcrt'],
                                },
                            },
                        ],
                    };

                    window.showSaveFilePicker(options).then((handle) => {
                        (async () => {
                            // FileSystemWritableFileStream 
                            const writable = await handle.createWritable();
                            //
                            const text = DataUtil.gZip(JSON.stringify(baseData));
                            await writable.write(text);
                            //
                            await writable.close();
                        })();
                    }).catch(() => {
                        console.log('');
                    });

                }
            },
            {
                name: 'Import',
                isEnable: true, callback: () => {
                    (async () => {
                        const [fileHandle] = await window.showOpenFilePicker();
                        const file = await fileHandle.getFile();
                        const fileContents = await file.text();
                        const text = DataUtil.unZip(fileContents);
                        console.log(text);
                        setThamaData(JSON.parse(text));
                    })();
                }
            },
            {
                name: 'Clear',
                isEnable: true, callback: () => {
                    baseData.meths[0].defs = [];
                    baseData.meths[1].defs = [];
                    baseNode.children[0].children = [];
                    baseNode.children[1].children = [];
                    invalidate();
                }
            },
        ]} />
        <ThemaEditTabUtil.InputTextForm titleLabel="name" value={themaName} setValue={(value: string) => {
            setThemaName(value);
        }} />
        <ThemaEditTabUtil.BorderPanel innerJsx={<>
            <ThemaEditTabUtil.LabelTextItem titleLabel="Method" value="sustain" />

            <ThemaEditTabUtil.InputTextForm titleLabel="Preset" value={presetSust} setValue={(value: string) => {
                setPresetSust(value);
            }} />
            <ThemaEditTabUtil.ButtonDiv buttons={[
                {
                    name: 'Add',
                    isEnable: presetSust !== '' && !presetSustList.includes(presetSust), 
                    callback: () => {
                        presetSustList.push(presetSust);
                        setPresetSustList(presetSustList.slice());
                        setPresetSust('');
                    }
                },
            ]} />
            <ThemaEditTabUtil.RecordList
                height={100}
                records={presetSustList.map((preset, i) => {
                    return {
                        label: preset, buttons: [
                            {
                                label: '', width: 30,
                                isEnable: i < presetSustList.length - 1,
                                callback: () => {
                                    const cache = presetSustList[i];
                                    presetSustList[i] = presetSustList[i + 1];
                                    presetSustList[i + 1] = cache;
                                    setPresetSustList(presetSustList.slice());
                                },
                            },
                            {
                                label: '', width: 30,
                                isEnable: i > 0,
                                callback: () => {
                                    const cache = presetSustList[i];
                                    presetSustList[i] = presetSustList[i - 1];
                                    presetSustList[i - 1] = cache;
                                    setPresetSustList(presetSustList.slice());
                                },
                            },
                            {
                                label: 'Delete', width: 60, callback: () => {
                                    presetSustList.splice(i, 1);
                                    setPresetSustList(presetSustList.slice());
                                },
                            },
                        ]
                    };
                })} />
            <ThemaEditTabUtil.ButtonDiv buttons={[
                {
                    name: 'Add attribute node',
                    isEnable: true, callback: () => {
                        const attrData: ThemaUtil.LevelAttr = {
                            attr: 'triad',
                            symbols: [],
                            roots: []
                        };
                        (metSusData.defs as ThemaUtil.LevelAttr[]).push(attrData);
                        const newNode = TreeUtil.createNode(attrData, metSusNode);
                        metSusNode.children.push(newNode);
                        setFocusNode(newNode);
                        invalidate();
                    }
                },
            ]} />
        </>} />
        <ThemaEditTabUtil.BorderPanel innerJsx={<>
            <ThemaEditTabUtil.LabelTextItem titleLabel="Method" value="pattern" />

            <ThemaEditTabUtil.InputTextForm titleLabel="Preset" value={presetPatt} setValue={(value: string) => {
                setPresetPatt(value);
            }} />
            <ThemaEditTabUtil.ButtonDiv buttons={[
                {
                    name: 'Add',
                    isEnable: presetPatt !== '' && !presetPattList.includes(presetPatt),
                    callback: () => {
                        presetPattList.push(presetPatt);
                        setPresetPattList(presetPattList.slice());
                        setPresetPatt('');
                    }
                },
            ]} />
            <ThemaEditTabUtil.RecordList
                height={100}
                records={presetPattList.map((preset, i) => {
                    return {
                        label: preset, buttons: [
                            {
                                label: '', width: 30,
                                isEnable: i < presetPattList.length - 1,
                                callback: () => {
                                    const cache = presetPattList[i];
                                    presetPattList[i] = presetPattList[i + 1];
                                    presetPattList[i + 1] = cache;
                                    setPresetPattList(presetPattList.slice());
                                },
                            },
                            {
                                label: '', width: 30,
                                isEnable: i > 0,
                                callback: () => {
                                    const cache = presetPattList[i];
                                    presetPattList[i] = presetPattList[i - 1];
                                    presetPattList[i - 1] = cache;
                                    setPresetPattList(presetPattList.slice());
                                },
                            },
                            {
                                label: 'Delete', width: 60, callback: () => {
                                    presetPattList.splice(i, 1);
                                    setPresetPattList(presetPattList.slice());
                                },
                            },
                        ]
                    };
                })} />
            <ThemaEditTabUtil.ButtonDiv buttons={[
                {
                    name: 'Add sustain node',
                    isEnable: true, callback: () => {
                        const sustData: ThemaUtil.LevelSust = {
                            beat: 4,
                            head: 0,
                            tail: 0,
                            patts: []
                        };
                        (metPatData.defs as ThemaUtil.LevelSust[]).push(sustData);
                        const newNode = TreeUtil.createNode(sustData, metPatNode);
                        metPatNode.children.push(newNode);
                        setFocusNode(newNode);
                        invalidate();
                    }
                },
            ]} />
        </>} />
        <ThemaEditTabUtil.ButtonDiv buttons={[
            {
                name: 'Update',
                isEnable: true, callback: () => {
                    baseData.themaName = themaName;
                    metSusData.presets = presetSustList.slice();
                    metPatData.presets = presetPattList.slice();
                    invalidate();
                }
            },
        ]} />
        <ThemaEditTabUtil.ButtonDiv buttons={[
            {
                name: 'Install',
                isEnable: !props.themaList.map(thema => thema.themaName).includes(baseData.themaName),
                callback: () => {
                    props.addThema(baseData);
                }
            }
        ]} />
        <ThemaEditTabUtil.RecordList
            height={100}
            records={props.themaList.map((thema, i) => {
                return {
                    label: thema.themaName, buttons: [
                        {
                            label: '', width: 30,
                            isEnable: i < props.themaList.length - 1,
                            callback: () => {
                                const cache = props.themaList[i];
                                props.themaList[i] = props.themaList[i + 1];
                                props.themaList[i + 1] = cache;
                                invalidate();
                            },
                        },
                        {
                            label: '', width: 30,
                            isEnable: i > 0,
                            callback: () => {
                                const cache = props.themaList[i];
                                props.themaList[i] = props.themaList[i - 1];
                                props.themaList[i - 1] = cache;
                                invalidate();
                            },
                        },
                        {
                            label: 'Load', width: 50, callback: () => {

                            },
                        },
                        {
                            label: 'Delete', width: 60, callback: () => {
                                props.themaList.splice(i, 1);
                                invalidate();
                            },
                        },
                    ]
                };
            })} />
    </>);
}

export default BaseFormTab;

